








import store from '../../modules/store';
import axios from 'axios';

export default {
  name: 'Login',
  data() {
    return {
      isAuthenticated: store.isAuthenticated
    };
  },
  methods: {
    logout() {
      axios({
        method: 'GET',
        url: '/api/accounts/logout'
      })
    }
  },
};

